// Backend uses shop_owner/car_owner but we map to friendlier names in UI
export type UserRole = 'car_owner' | 'shop_owner';

export interface SignupRequest {
  email: string;
  password: string;
  confirmPassword: string;
  first_name: string;
  last_name: string;
  role: UserRole;
  phone_number?: string;
  address?: string;
  city?: string;
  state?: string;
  zip_code?: string;
}

export interface LoginRequest {
  email: string;
  password: string;
}

export interface User {
  id: number;
  email: string;
  first_name: string;
  last_name: string;
  name?: string; // Combined name for display
  role: UserRole;
  phone_number?: string;
  address?: string;
  city?: string;
  state?: string;
  zip_code?: string;
  created_at: string;
  updated_at: string;
}

export interface ValidationError {
  field: string;
  message: string;
}

export interface AuthErrorResponse {
  success: false;
  error: {
    message: string;
    details?: ValidationError[];
  };
}

export interface AuthResponse {
  success: boolean;
  message?: string;
  data?: User;
  token?: string;
  error?: {
    message: string;
    details?: ValidationError[];
  };
}

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || 'http://localhost:5000';

// Token management
export class TokenManager {
  private static readonly TOKEN_KEY = 'motortrust_token';

  static setToken(token: string): void {
    if (typeof window !== 'undefined') {
      localStorage.setItem(this.TOKEN_KEY, token);
    }
  }

  static getToken(): string | null {
    if (typeof window !== 'undefined') {
      return localStorage.getItem(this.TOKEN_KEY);
    }
    return null;
  }

  static removeToken(): void {
    if (typeof window !== 'undefined') {
      localStorage.removeItem(this.TOKEN_KEY);
    }
  }

  static isAuthenticated(): boolean {
    return this.getToken() !== null;
  }
}

// Helper to format validation errors
export function formatValidationErrors(details: ValidationError[]): string {
  return details.map(d => `${d.field}: ${d.message}`).join('\n');
}

export class AuthService {
  private static getAuthHeader(): string {
    const token = TokenManager.getToken();
    if (!token) throw new Error('No authentication token found');
    return `Bearer ${token}`;
  }

  static async signup(data: SignupRequest): Promise<AuthResponse> {
    const response = await fetch(`${API_BASE_URL}/api/auth/signup`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    });

    const result = await response.json();

    if (!response.ok) {
      // Handle validation errors
      if (result.error?.details && Array.isArray(result.error.details)) {
        const errorMessages = result.error.details.map((d: ValidationError) => d.message).join(', ');
        throw new Error(errorMessages || result.error?.message || 'Signup failed');
      }
      throw new Error(result.error?.message || result.message || 'Signup failed');
    }

    // Store token
    if (result.token) {
      TokenManager.setToken(result.token);
    }

    return result;
  }

  static async login(data: LoginRequest): Promise<AuthResponse> {
    const response = await fetch(`${API_BASE_URL}/api/auth/login`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    });

    const result = await response.json();

    if (!response.ok) {
      if (result.error?.details && Array.isArray(result.error.details)) {
        const errorMessages = result.error.details.map((d: ValidationError) => d.message).join(', ');
        throw new Error(errorMessages || result.error?.message || 'Login failed');
      }
      throw new Error(result.error?.message || result.message || 'Login failed');
    }

    // Store token
    if (result.token) {
      TokenManager.setToken(result.token);
    }

    return result;
  }

  static async getCurrentUser(): Promise<AuthResponse> {
    const authHeader = this.getAuthHeader();
    const response = await fetch(`${API_BASE_URL}/api/auth/me`, {
      method: 'GET',
      headers: {
        'Authorization': authHeader,
      },
    });

    const result = await response.json();

    if (!response.ok) {
      throw new Error(result.error?.message || result.message || 'Failed to get user data');
    }

    // Add combined name for convenience
    if (result.data && result.data.first_name) {
      result.data.name = `${result.data.first_name} ${result.data.last_name || ''}`.trim();
    }

    return result;
  }

  static async logout(): Promise<AuthResponse> {
    try {
      const authHeader = this.getAuthHeader();
      const response = await fetch(`${API_BASE_URL}/api/auth/logout`, {
        method: 'POST',
        headers: {
          'Authorization': authHeader,
        },
      });

      const result = await response.json();

      if (!response.ok) {
        throw new Error(result.error?.message || result.message || 'Logout failed');
      }

      return result;
    } finally {
      // Always clear token on client side
      TokenManager.removeToken();
    }
  }
}
